
*{
  text-align: left;
}
.demo-infinite-container{
  width: 100%;
  height: 100vh;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  border: 1px solid #d9d9d9;
  padding-top: 110px;
}
.list-container{
  padding: 12px;
  background-color: white;
}
.product-container{
  margin-left: 12px;
  display: inline-block;
  width: calc(100% - 124px);
  vertical-align: top;
}
.product-container > p{
  font-family: PingFang-SC-Regular;
  font-size: 14px;
  color: #4B494D;
  letter-spacing: 0;
  line-height: 15px;
  font-weight: 600;
}
.product-container article{
  margin-top: 11px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #676767;
  letter-spacing: 0;
}
.product_price{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  margin-top: 11px;
}
.product_price > span{
  font-size: 12px;
  color: #a4a4a4;
}
.product_price .safe-chip{
  width: auto;
  border: 1px solid #87C67D;
  color: #87C67D;
  border-radius: 4px;
  padding: 2px 10px 2px 10px;
  font-size: 10px;
  letter-spacing: 1px;
}
.product-img{
  border-radius: 4px;
  width: 100px;
  height: 100px;
}
.choose-container{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  padding-top: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #E8E8E8;
  background-color: white;
}
.choose-container li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  list-style: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  /*text-align: center;*/
}
.choose-container li > i {
  margin-left: 5px;
}
.search-result-container{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 7px 12px 7px 12px;
  border-bottom: 1px solid #E8E8E8;
  background-color: white;
}
.search-result-container > i{
  font-size: 36px;
  color: #4B494D;
}
.search-border{
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  margin-left: 12px;
  padding: 6px 8px 6px 8px;
  background-color: #F5F5F5;
  border-radius: 100px;
}
.search-border > i{
  color:#D8D8D8;
  font-size: 16px;
}
.search-border input{
  color: #323233;
  outline: none;
  border: none;
  background-color: transparent;
}
.fix-top{
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
}



