
*{
  text-align: left;
}
.demo-infinite-container{
  width: 100%;
  height: 100vh;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  border: 1px solid #d9d9d9;
  padding-top: 35px;
}
.list-container{
  position: relative;
  padding: 12px;
  background-color: white;
}
.product-container{
  margin-left: 16px;
  display: inline-block;
  width: calc(100% - 124px);
  vertical-align: middle;
}
.product-container > p:nth-child(1){
  font-size: 14px;
  color: #4C4B4C;
  line-height: 14px;
}
.product-container > p:nth-child(2){
  margin-top: 12px;
  font-size: 12px;
  color: #656466;
  letter-spacing: 0;
  line-height: 12px;
}
.product-container article{
  margin-top: 11px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #676767;
  letter-spacing: 0;
}
.product-img{
  border-radius: 4px;
  width: 64px;
  height: 64px;
}
.choose-container li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  list-style: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  /*text-align: center;*/
}
.choose-container li > i {
  margin-left: 5px;
}
.search-result-container > i{
  font-size: 36px;
  color: #4B494D;
}
.search-border > i{
  color:#D8D8D8;
  font-size: 16px;
}
.search-border input{
  color: #323233;
  outline: none;
  border: none;
  background-color: transparent;
}
#forward{
  position: absolute;
  top: 50%;
  right: 12px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}



