
.container {
  text-align: left;
  background-color: #F5F5F5;
}
.container > img {
  margin-top: 44px;
  background-color: #e8e8e8;
}
.find-detail {
  margin-top: -8px;
  padding: 12px;
  background-color: white;
}
.find-detail > h1 {
  margin-top: 10px;
  font-size: 18px;
  font-weight: 400;
}
.find-detail > span {
  color: #AFACB3;
  font-size: 12px;
}

/*标签*/
.tag-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  padding-right: 12px;
}
.tag-container li {
  margin-top: 10px;
  margin-right: 10px;
  list-style: none;
  padding: 5px 12px 5px 12px;
  background-color: #f4f4f4;
  border-radius: 8px;
  color: #7D7A7F;
  font-size: 11px;
}
.tag-container li:first-child {
  margin-left: 0;
}

/*用户信息*/
.user-info {
  margin-top: 20px;
  padding: 0 12px 12px 12px;
  background-color: white;
}
.user-info img, article {
  display: inline-block;
  vertical-align: top;
}
.user-info > img {
  margin-top: 2px;
  border-radius: 50%;
}
.user-info article {
  margin-left: 9px;
}
.user-info article p:nth-child(1) {
  font-size: 14px;
  color: #4B494C;
}
.user-info article p:nth-child(2) {
  margin-top: 2px;
  font-size: 10px;
  color: #646266;
}
.main-img {
  width: 100%;
}
.main-article {
  line-height: 26px;
  font-size: 14px;
  color: #4B494C;
  text-indent: 2em;
  white-space: pre-wrap;
}
.main-product {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 8px;
  margin-bottom: 8px;
  padding: 2px 12px 2px 12px;
  border: 1px solid #F0F0F0;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
}
.main-product section {
  margin-left: 10px;
}
.main-product section h2 {
  font-size: 16px;
  color: #4B4A4D;
  font-weight: 400;
}
.main-product section p {
  margin-top: 5px;
  font-size: 12px;
  color: #7C7A7F;
}
.created-time {
  margin-top: 20px;
  color: #AFACB3;
  font-size: 12px;
  text-align: right;
}
.comment-num {
  margin-top: 8px;
  text-align: center;
  font-size: 16px;
  color: #4B494C;
  line-height: 44px;
  border-top: 1px solid #D9D9D9;
  background-color: white;
}
.hot-comment {
  padding-left: 9px;
  text-align: left;
  border-left: 4px solid #AC69FE;
  font-size: 12px;
  color: #323133;
  background-color: white;
}
.main-find {
  padding-top: 44px;
  background-color: white;
}

/* 嵌套h5样式 */
.img_z {
  width: 100%;
  max-height: 220px;
}
.page_info_main01 {
  padding: 12px;
}
.page_main_title {
  font-size: 18px;
  color: #4b494c;
}
.page_lists_box li {
  display: inline-block;
  list-style: none;
  font-size: 12px;
  margin-top: 32px;
  margin-right: 8px;
  padding: 5px 12px 5px 12px;
  border: 1px solid #4b494c;
  border-radius: 100px;
}
#author_href {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.cp_img {
  position: relative;
}
.Vicon {
  position: absolute;
  right: 0;
  bottom: 0;
}
.Vicon img {
  width: 15px;
  height: 15px;
}
.cp_info {
  margin-left: 12px !important;
}
.cp_info > .cp_name {
  font-size: 14px;
  color: #ac69fe;
}
.cp_info > .cp_jiage {
  font-size: 12px;
  color: #666666;
}
.card_content {
  padding: 12px;
  font-size: 14px;
  line-height: 21px;
  color: #4b494c;
}
.news_image {
  margin-left: 12px;
  margin-right: 12px;
  width: calc(100% - 24px);
}
.card_sub_title {
  margin-left: 12px;
  color: #ac69fe;
  font-size: 16px;
  font-weight: 400;
}
.card_title {
  margin-left: 12px;
  font-size: 16px;
  color: #4b494c;
  font-weight: 400;
}



