
.container {
  background-color: #F5F5F5;
}
.tabs {
  margin-top: 4px;
  background-color: white;
  border-bottom: 1px solid #F5F5F5;
}
.tabs-title {
  color: #313233;
}
.bottom-line {
  background-color: #AC69FE;
}

/*成分分析*/
.com-analysis {
  height: 166px;
  padding: 15px 38px 18px 38px;
  background-color: white;
}
#record {
  font-size: 12px;
  color: #7C797F;
  text-align: center;
}
#record a {
  text-decoration: none;
  color: #AC69FE;
  text-align: center;
}
.composition-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  margin-top: 12px;
  width: 100%;
  height: 18px;
}
.composition-container div:nth-child(1) {
  position: absolute;
  left: 38px;
  z-index: 0;
  /*width: calc(60% + 15px);*/
  width: calc(100% - 76px);
  height: 18px;
  background-color: #87C67D;
  border-radius: 50px;
}
.composition-container div:nth-child(2) {
  position: relative;
  z-index: 1;
  margin-right: -10px;
  /*width: calc(30% + 15px);*/
  height: 100%;
  background-color: #F7CC36;
  border-radius: 50px;
}
.composition-container div:nth-child(3) {
  position: relative;
  z-index: 2;
  width: 10%;
  height: 100%;
  background-color: #FF7972;
  border-radius: 50px;
}
.composition-intro {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-top: 12px;
  width: 100%;
  height: 18px;
}
.composition-intro > div p, img {
  display: inline-block;
  vertical-align: middle;
}
.composition-intro > div p {
  margin-left: 6px;
  font-size: 12px;
  color: #323233;
}
.composition-intro > div:nth-child(1) p {
  color: #87C67D;
}
.composition-intro > div:nth-child(2) p {
  color: #F7CC36;
}
.composition-intro > div:nth-child(3) p {
  color: #FF574F;
}
.check-comp {
  margin: 23px auto;
  width: 143px;
  padding: 5px 12px 5px 12px;
  border: 1px solid #AC69FE;
  border-radius: 3px;
  font-size: 13px;
  color: #AC69FE;
  letter-spacing: 1px;
}

/*安全说，功效说*/
.comp-item-second {
  height: 166px;
  padding: 16px 12px 16px 12px;
  background-color: white;
}
.comp-item-second > p:nth-child(1) {
  text-align: center;
}
.flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  margin-top: 17px;
}
.flex-container p {
  margin-left: 12px;
  width: calc(50% - 6px);
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #F8F8F8;
  text-align: center;
  font-size: 14px;
  color: #323233;
}
.flex-container p:nth-child(3), p:nth-child(4) {
  margin-top: 12px;
}
.flex-container p:nth-child(1), p:nth-child(3) {
  margin-left: 0;
}

/*肤质匹配*/
.comp-item-four {
  height: 166px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: white;
}
.comp-item-four p {
  width: 200px;
  padding: 10px 0 10px 0;
  font-size: 14px;
  color: #323233;
  text-align: center;
  background-color: #F8F8F8;
}
.comp-item-four p:nth-child(2) {
  margin-top: 12px;
}
.product-detail {
  margin-top: 44px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  padding: 16px 12px 16px 12px;
  background-color: white;
}
.product-detail-intro {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  margin-left: 12px;
  text-align: left;
}
.product-detail-intro p:nth-child(1) {
  font-size: 15px;
  color: #313033;
  line-height: 22px;
}
.product-detail-intro p:nth-child(2) {
  margin-top: 2px;
  font-size: 12px;
  color: #313033;
  line-height: 18px;
}
.product-detail-intro p:nth-child(3) {
  margin-top: 2px;
  font-size: 12px;
  color: #959199;
  line-height: 18px;
}
.product-detail-intro p:nth-child(4) {
  display: inline-block;
  font-size: 12px;
  color: #676767;
  letter-spacing: 0;
}
.mention-user-part {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 44px;
  margin-top: 4px;
  background-color: white;
  border-bottom: 2px solid #F5F5F5;
}
.mention-user-part p {
  font-size: 15px;
  color: #323133;
}
.mention-user-part img {
  margin-left: 5px;
  width: 18px;
  height: 18px;
}

/*评论*/
.comment-container {
  margin-top: 4px;
}
.comment-title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 12px;
  width: 100%;
  background-color: white;
  border-bottom: 1px solid #D9D9D9;
}
.comment-title p {
  font-size: 15px;
  color: #4B494C;
}
.comment-title > div p, img {
  display: inline-block;
}
.comment-title > div p {
  font-size: 12px;
  color: #AC69FE;
}
.hot-comment {
  padding: 0 12px 0 12px;
  font-size: 12px;
  color: #323133;
  border-left: 2px solid #AC69FE;
  text-align: left;
  background-color: white;
}
.comment-main {
  padding-top: 6px;
  background-color: white;
}

/* 骨架屏 */
.load-img {
  background-color: #e8e8e8;
}
.load-p > p:nth-child(1) {
  height: 20px;
  background-color: #e8e8e8;
}
.load-p > p:nth-child(2) {
  margin-top: 15px;
  width: 70%;
  height: 20px;
  background-color: #e8e8e8;
}
.load-p > p:nth-child(3) {
  margin-top: 10px;
  width: 30%;
  height: 15px;
  background-color: #e8e8e8;
}
.load-p > p:nth-child(4) {
  width: 50%;
  height: 15px;
  background-color: #e8e8e8;
}
.flex-container  .font-active{
  color: #ac69fe;
}



